.mapCenter {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-15px, -15px);
  pointer-events: none;
  .vertical,
  .horizontal {
    width: 4px;
    height: 30px;
    position: absolute;
    left: 13px;
    background-color: black;
  }

  .horizontal {
    transform: rotate(90deg);
  }
}
